<template>
  <v-dialog v-model="join_dialog" persistent  fullscreen>
    <v-card>
      <v-toolbar color="#1E88E5">
        <v-card-title>
          <span class="headline" style="color:white;"></span>
        </v-card-title>
        <v-spacer></v-spacer>
        <v-btn icon color="white" @click="formClose">
          <v-icon size="40">mdi-close-box</v-icon>
        </v-btn>
      </v-toolbar>
      <div class="form_area">
        <v-card-text>
          <v-container>
            <v-row>
              <v-col cols="12">
              <center>
                <img src="../../images/logo.png" width="200">
              </center>
                <v-text-field
                ref="member_name"
                v-model="member_name"
                :rules="[() => !!member_name || ' .']"
                :error-messages="errorMessages"
                label=""
                clearable
                required
                ></v-text-field>
              </v-col>
              <v-col cols="12" sm="8" xs="12">
                  <v-text-field
                  ref="member_email"
                  v-model="member_email"
                  :rules="emailRules"
                  :error-messages="errorMessages"
                  label=""
                  clearable
                  required
                  :disabled="email_disabled"
                  @blur="duplicateEmail"
                  ></v-text-field>
              </v-col>
              <v-col cols="12" sm="4" xs="12">
                    <v-btn block height="50px" class="font-weight-bold"  @click="sendCertifyNumber"> </v-btn>
              </v-col>
              <v-col cols="12">
                <v-text-field
                  ref="certify_number"
                  v-model="certify_number"
                  :rules="[() => !!certify_number || ' .']"
                  :error-messages="errorMessages"
                  type="password"
                  label=""
                  clearable
                  required
                ></v-text-field>
              </v-col>
              <v-col cols="12" sm="6" xs="12">
                <v-text-field
                ref="member_password"
                v-model="member_password"
                :rules="passwordRules"
                :error-messages="errorMessages"
                label=""
                clearable
                required
                type="password"
                ></v-text-field>
              </v-col>
              <v-col cols="12" sm="6" xs="12">
                <v-text-field
                ref="member_password_check"
                v-model="member_password_check"
                :rules="passwordCheckRules"
                :eroor-messages="errorMessages"
                type="password"
                label=" "
                clearable
                required
                ></v-text-field>
              </v-col>
              <v-btn block height="60px" color="primary" @click="submit"></v-btn>
            </v-row>
          </v-container>
        </v-card-text>
        <v-card-actions>
          <v-btn color="blue darken-1" class="font-weight-bold" text @click="dataReset"></v-btn>
          <v-spacer/>
          <v-btn color="blue darken-1" class="font-weight-bold" text @click="formClose"></v-btn>
        </v-card-actions>
      </div>
    </v-card>
  </v-dialog>
</template>

<script>
  export default {
    data () {
      return {
        join_dialog: false, 
        member_email:null, 
        certify_number:null, 
        member_password:null, 
        member_password_check:null,
        member_name:null,
        emailRules: [
          v => !!v || ' .',
          v => /.+@.+/.test(v) || '   .',
        ],
        passwordRules:[
          v => v === null ? ' .'
          : v.length >= 8 && v.length <=25 || '8~25 , ,  .',
          v => /[0-9]/g.test(v) || '  .',
          v => /[a-z]/ig.test(v) || '  .',
          v => /[`~!@@#$%^&*|'";:/?]/gi.test(v) || '  .',
        ],
        passwordCheckRules:[
          v => !!v || '   .',
          v => v === this.member_password || '  .',
        ],
        errorMessages: '',
        formHasErrors: false, 
        email_disabled: false, 
      }
    },
    props: ['login_info'],
    computed:{
      //   
      form(){
        return{
          member_name:this.member_name,
          member_email: this.member_email,
          certify_number:this.certify_number,
          member_password:this.member_password,
          member_password_check:this.member_password_check,
        }
      }
    },
    methods:{
      //  
      duplicateEmail(){
        const {member_email} = this.$refs;
        const num = member_email.errorBucket.length;

        if(!num){
          this.$http.get("/api/join/duplicate_email/" + this.member_email).then(res => {
            if(!!res.data.code && res.data.result > 0){
              member_email.errorBucket.push("   .")
            }
          }).catch(err => {
            console.log(err);
          })
        }
      },
      //      
      joinOrMypageButton(){
        if(!!this.login_info.number){
          this.$router.push('/memberPage')
        }else{
          this.join_dialog = true;
        }
      },
      //    
      dataReset(){  
        this.errorMessages = [] 
        this.formHasErrors = false 

        Object.keys(this.form).forEach(f => {
          this.$refs[f].isResetting = true;
          this.$refs[f].internalValue = Array.isArray(this.internalValue) ? [] : null;
        })
        this.email_disabled = false;
      },
      // 
      formClose(){
        this.dataReset();
        this.join_dialog = false;
      },
      //  
      //       
      submit () {
        this.formHasErrors = false

        Object.keys(this.form).forEach(f => {
          if (!this.form[f] || this.$refs[f].hasError) {
            this.formHasErrors = true;
            this.$refs[f].validate(true); //   
          }
        })
        
        if(!this.formHasErrors){
          const item = {
            'email' : this.member_email,
            'name' : this.member_name,
            'certify_number' : this.certify_number,
            'password' : this.member_password
          }

          this.$http.post("/api/join/input_member", item).then(res => {
            if(!!res.data.code){
              if(res.data.result){
                alert('  ,   .')
                return;
              }
              this.member_password = [];
              this.member_password_check = [];
              alert(" .");
              this.formClose();
            }
          }).catch(err => {
            console.log(err);
            alert("   .");
          })
        }
      },
      //   
      //     
      sendCertifyNumber(){
        let email_field = this.$refs["member_email"]
        if(!email_field.hasError){ 
          this.email_disabled = true;
          const item = {email : this.member_email};
          this.$http.post ('/api/join/certify-number', item).then(res => {
            if(!res.data.code || !res.data.result){
              this.email_disabled = false;
            }else{
              alert(' .')
            }
          }).catch(err => {
            alert('    .');
            this.email_disabled = false;
          })
        }else{
          email_field.validate(true)
        }
      },

    }
  }

</script>