<template>
    <v-layout wrap>
        <v-col cols="12" sm="8" xs="12">
              <v-text-field
              ref="member_email"
              v-model="member_email"
              :rules="emailRules"
              :error-messages="errorMessages"
              label=""
              clearable
              required
              :disabled="email_disabled"
              @blur="duplicateEmail"
              ></v-text-field>
          </v-col>
          <v-col cols="12" sm="4" xs="12">
                <v-btn block height="50px" class="font-weight-bold" :loading="loading" @click="sendCertifyNumber"> </v-btn>
          </v-col>
          <v-col cols="12">
            <v-text-field
              ref="certify_number"
              v-model="certify_number"
              :rules="[() => !!certify_number || ' .']"
              :error-messages="errorMessages"
              type="password"
              label=""
              clearable
              required
            ></v-text-field>
          </v-col>
          <v-col cols="12" sm="6" xs="12">
            <v-text-field
            ref="member_password"
            v-model="member_password"
            :rules="passwordRules"
            :error-messages="errorMessages"
            label=""
            clearable
            required
            :type="showPW ? 'text' : 'password'"
            ></v-text-field>
          </v-col>
          <v-col cols="12" sm="6" xs="12">
            <v-text-field
            ref="member_password_check"
            v-model="member_password_check"
            :rules="passwordCheckRules"
            :eroor-messages="errorMessages"
            :type="showPW ? 'text' : 'password'"
            label=" "
            clearable
            required
            ></v-text-field>
          </v-col>
          <v-col cols="12">
            <v-btn block height="60px" color="primary" @click="submit"> </v-btn>
          </v-col>
    </v-layout>
</template>

<script>
export default {
    data () {
      return {
        member_email:null,
        certify_number:null,
        member_password:null,
        member_password_check:null,
         emailRules: [
          v => !!v || ' .',
          v => /.+@.+/.test(v) || '   .',
        ],
        passwordRules:[
          v => v === null ? ' .'
          : v.length >= 8 && v.length <=25 || '8~25 , ,  .',
          v => /[0-9]/g.test(v) || '  .',
          v => /[a-z]/ig.test(v) || '  .',
          v => /[`~!@@#$%^&*|'";:/?]/gi.test(v) || '  .',
        ],
        passwordCheckRules:[
          v => !!v || '   .',
          v => v === this.member_password || '  .',
        ],
        email_disabled:false,
        errorMessages: '',
        formHasErrors: false, 
        loading: false,
      }
    },
    computed:{
      //    
      form(){
        return{
          member_email: this.member_email,
          certify_number:this.certify_number,
          member_password:this.member_password,
          member_password_check:this.member_password_check,
        }
      }
    },
    methods:{
      //   
      dataReset(){  
        this.errorMessages = [] 
        this.formHasErrors = false 

        Object.keys(this.form).forEach(f => {
          this.$refs[f].isResetting = true;
          this.$refs[f].internalValue = Array.isArray(this.internalValue) ? [] : null;
        })

        this.email_disabled = false;
      },
      // 
      sendCertifyNumber(){
        let email_field = this.$refs["member_email"]

        if(!email_field.hasError){ 
          this.email_disabled = true;
          this.loading = true;
          const item = {email : this.member_email, bl:false};

          this.$http.post ('/api/member/certify_number', item).then(res => {
            this.loading = false;
            if(!res.data.code){
              alert('    .');
            }else if(!res.data.result){
              alert('  .')
            }else{
              alert(' .');
              return;
            }
            this.email_disabled = false;
          }).catch(err => {
            alert('    .');
            this.loading = false;
            this.email_disabled = false;
          })
        }else{
          email_field.validate(true)
        }
      },
      //  
      submit(){
        this.formHasErrors = false

        Object.keys(this.form).forEach(f => {
          if (!this.form[f] || this.$refs[f].hasError) {
            this.formHasErrors = true;
            this.$refs[f].validate(true); //   
          }
        })
        
        if(!this.formHasErrors){
          const item = {
            'email' : this.member_email,
            'certify_number' : this.certify_number,
            'password' : this.member_password
          }

          this.$http.post("/api/member/find_pass", item).then(res => {
            if(!!res.data.code){
              if(res.data.result){
                alert('  ,   .')
                return;
              }
              alert("  .");
              this.dataReset();
              this.$emit('child');
            }
          }).catch(err => {
            console.log(err);
            alert("   .");
          })
        }
      }
    }
}
</script>
