<template>
  <v-dialog v-model="login_dialog" persistent fullscreen>
    <v-card>
      <v-toolbar color="#1E88E5">
        <v-card-title>
          <span class="headline" style="color:white;"></span>
        </v-card-title>
        <v-spacer></v-spacer>
        <v-btn icon color="white" @click="formClose">
          <v-icon size="40">mdi-close-box</v-icon>
        </v-btn>
      </v-toolbar>
      <div class="form_area">
        <br>
        <v-container>
          <v-row>
            <v-col cols="12">
              <center>
                <img src="../../images/logo.png" width="200">
              </center>
              <br><br>
              <v-tabs-items v-model="tab">
                <v-tab-item>
                <v-text-field
                ref="user_email"
                v-model="user_email"
                :rules="[() => !!user_email || ' .']"
                :eroor-messages="errorMessages"
                label=""
                filled clearable
                @keyup.enter="tryLogin"
                ></v-text-field>
                <v-text-field
                ref="user_pw"
                v-model="user_pw"
                :rules="[() => !!user_pw || ' .']"
                :eroor-messages="errorMessages"
                type="password"
                label=""
                filled clearable
                @keyup.enter="tryLogin"
                ></v-text-field>
                <span style="color:red; font-size:12px">{{ex_message}}</span>
                <br><br>
                <v-btn block height="60px" color="primary" @click="tryLogin"></v-btn>
                <br>
                <NLB/>
                </v-tab-item>
                <v-tab-item>
                  <PWC
                  @child="tab = 0"/>
                </v-tab-item>
              </v-tabs-items>
            </v-col>
          </v-row>
        </v-container>
        <v-card-actions>
          <div class="dif_area">
            <v-btn color="primary" v-text="!tab ? ' ' : ' '"
            text @click="tab = tab ? 0 : 1" ></v-btn>
            <v-btn color="primary" text @click="moveToJoin"></v-btn>
          </div>
        </v-card-actions>
      </div>
    </v-card>
  </v-dialog>
</template>

<script>
import NLB from './NaverLoginButton'//  
import PWC from "./PWC";
  export default {
    data: () => ({
      tab:0,
      login_dialog: false, //  
      errorMessages: '', //    
      user_email: null, // 
      user_pw: null, // 
      ex_message : '', //  
      formHasErrors: false,//      
    }),
    props: ['login_info'],
    computed:{
      //   
      form(){
        return{
          user_email:this.user_email,
          user_pw:this.user_pw,
        }
      },
      
    },
    methods :{
      //  , 
      inOrOut_button(){
        if(this.login_info.number !== 0){
          this.$http.get("/api/login_spv/logout").then((res) => {
              this.$emit("child", {
                number: 0,
                l_title: "LOGIN",
                r_title: "SIGN UP"
              });
          }).catch(err => {
            this.$router.go();
          });
        }else{
          this.login_dialog = true;
        }
      },
      //  ,   
      moveToJoin(){
        this.formClose();
        this.$emit('join');
      },
      //  
      dataReset(){  
        this.errorMessages = [] 
        this.formHasErrors = false 
        Object.keys(this.form).forEach(f => {
          this.$refs[f].reset()
        })
      },
      // 
      formClose(){
        this.dataReset();
        this.login_dialog = false;
      },
      // 
      //       
      tryLogin(){
        this.formHasErrors = false
        Object.keys(this.form).forEach(f => {
           if (!this.form[f] || this.$refs[f].hasError) {
            this.formHasErrors = true;
            this.$refs[f].validate(true)//   
          }
        })
        if(!this.formHasErrors){
          this.$http.post("/api/login", {'email' : this.user_email, 'password' : this.user_pw,}).then(
            (res) => {
              const rs = res.data.message
              if(!!res.data.message){
                this.$emit("child", {
                  number: 1,
                  l_title: "LOGOUT",
                  r_title: "MYPAGE"
                });
                this.formClose();
                // this.$router.go();
              }else{
                this.ex_message = "  ,  ."
              }
            }).catch(err => {
              alert(err);
            })
          }
        }
    },
    components:{
      NLB, PWC
    }
  }
</script>